.CompraPage {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
}
.Informacion {
  margin: auto;
  max-width: 30em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* min-width: 2em; */
}
.Informacion p:nth-child(1) {
  font-weight: 800;
  /* font-size: 1.2em; */
  margin-bottom: 2em;
}
.Informacion p a {
  margin: 0 4px;
  font-weight: 800;
  font-size: 1.1em;
  color: rgb(16, 110, 55);
}
.Informacion p:nth-child(2) {
  color: #444;
}
