}

.App-link {
  color: #61dafb;
}

.title {
  font-size: 3em;
}
.subtitle {
  font-size: 1.2em;
}
.btn {
  text-decoration: none;
  font-size: 1.1em;
  padding: 0.3em 1em;
  border: none;
  border-radius: 5px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  color: var(--color1);
  background: var(--color3);
  pointer-events: all;
}
.btn:hover {
  background: var(--color3hover);
  color: var(--color1hover);
}
.btn:active {
  color: var(--color1);
}
.btn svg {
  padding: 0px 20px;
}
.scrollBar::-webkit-scrollbar-track {
  background-color: var(--scrollBarBG);
  border-radius: 2px;
}

.scrollBar::-webkit-scrollbar {
  width: 10px;
  background-color: #0c0d24;
  border-radius: 2px;
}

.scrollBar::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: var(--scrollBar);
  background-image: -webkit-linear-gradient(
    90deg,
    transparent 10%,
    var(--scrollBar) 20%,
    var(--scrollBar) 80%,
    transparent 90%,
    transparent
  );
}

.ShopSection {
  display: -webkit-flex;
  display: flex;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.itemfade-enter {
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.itemfade-enter-active {
  opacity: 1;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.itemfade-leave {
  opacity: 1;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.itemfade-leave-active {
  opacity: 0;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.desktop {
  display: inline;
}
.stiky {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}
@media (max-width: 1000px) {
  .desktop {
    display: none;
  }
}
@media (min-width: 1000px) {
  .mobile {
    display: none;
  }
}

