  }
  62% {
    opacity: 1;
    content: "Remeras";
  }
  66% {
    opacity: 0;
  }
  71% {
    opacity: 1;
    content: "Pantalones";
  }
  95% {
    opacity: 1;
    content: "Pantalones";
  }
  100% {
    opacity: 0;
    content: "Pantalones";
  }
}

