  color: #fff;
  border-color: var(--inputColor);
}

.genero button {
  font-size: 1.6em;
  height: 1.5em;
  width: 1.5em;
  margin: 0px 5px 5px 0;
  border-radius: 0.5em;
  background-color: var(--inputColor);
  color: #666;
  border: var(--border);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.genero button svg {
  pointer-events: none;
}
.genero button:nth-child(1):hover {
